.left-menu {
  width: 560px;
  height: 850px;
  color: #FFFFFF;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.left-menu img.logo {
  width: 100%;
}
.left-menu .login {
  width: 80%;
  padding: 5px 0 5px 20%;
}
.left-menu .login div {
  padding: 10px;
  background-image: url(data:image/png;base64,)
}
.left-menu .login img {
  width: 20px;
}
.left-menu .login span {
  margin-left: 15px;
}
.left-menu .select {
  font-family: ASTOM;
  font-size: 20pt;
  margin-top: 30px;
}
.left-menu .select .item {
  display: -webkit-flex;
  display: flex;
  height: 111px;
  cursor: pointer;
}
.left-menu .select .item.selected {
  background: 
  url(data:image/png;base64,) no-repeat -36px,
  url(/static/media/light_launcher_right.2c660245.png) no-repeat 354px;
}
.left-menu .select .item .text {
  position: absolute;
  left: 100px;
  width: 200px;
  height: 111px;
  padding-top: 40px;
}
.left-menu .select .item.selected .text {
  font-size: 21pt;
  left: 154px;
  background:
  url(data:image/png;base64,) repeat-x;
}
.left-menu .select .item .text .sword {
  position: absolute;
  top: -15px;
  left: -80px;
  display: none;
}
.left-menu .select .item.selected .text .sword {
  left: -80px;
  display: block;
}
.left-menu .footer {
  margin-top: auto;
  padding-left: 150px;
  font-size: 12pt;
}
.left-menu .footer .playBtn {
  cursor: pointer;
}

.play {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.play .top-line {
  position: absolute;
  
}
.play div .mainImg {
  width: 100%;
}
.play .title {
  margin: 10px 0;
}
.play .content {
  padding: 15px 0 0 0;
  background: url(data:image/png;base64,) repeat-x;
}
.play .btns {
  text-align: center;
}
.play .btns img {
  margin: 0 5px;
  cursor: pointer;
}
.play .btns .off-dot {
  opacity: .2;
}
.play .more {
  position: relative;
  cursor: pointer;
}
.play .more div{
  position: absolute;
  top: -40px;
  left: 480px;
}
.play footer {
  margin-top: auto;
}
.terms {
  width: 100%;
  padding: 20px;
  border: 2px solid #1C222B;
}
.terms .title {
  font-size: 18pt;
}
.terms .content {
  font-size: 11pt;
  margin-top: 5px;
  height: 483px;
  overflow-y: auto;
  color: #676D71;
}
.terms .content h6 {
  font-size: 13pt;
}
::-webkit-scrollbar {
  width: 30px;
} 
::-webkit-scrollbar-track {
  background: url(data:image/png;base64,) repeat-y 20px 1px;
}
::-webkit-scrollbar-thumb {
  background: url(data:image/png;base64,) no-repeat 7px -2px;
  background-size: cover;
} 
@-webkit-keyframes blinker {
  50% {
    opacity: 0;
  }
}
@keyframes blinker {
  50% {
    opacity: 0;
  }
}
.contents {
  position: absolute;
  left: 0;
}
.contents.logo {
  top: 40%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.contents .logo-text {
  position: fixed;
  width: 100%;
  font-family: ASTOM;
  font-size: 20pt;
  color: #ffffff;
  text-align: center;
  text-shadow: 2px 2px #000000;
}
.contents .blicker {
  cursor: pointer;
  -webkit-animation: blinker 1s step-start infinite;
          animation: blinker 1s step-start infinite;
}
.contents img {
  vertical-align: middle;
}
.contents .main {
  display: -webkit-flex;
  display: flex;
}
.contents .container {
  display: -webkit-flex;
  display: flex;
  background: url(/static/media/bg_launcher_con_light.40dbeb38.png) no-repeat 190px 218px;
}
.box {
  display: -webkit-flex;
  display: flex;
  width: 628px;
  height: 560px;
  margin-left: 200px;
  padding: 20px;
  background:
  url(data:image/png;base64,) no-repeat;
  background-size: contain;
  color: #FFFFFF;
  background-color: #111619;
  -webkit-align-self: flex-end;
          align-self: flex-end;
}
.background {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
}
@font-face {
  font-family: ASTOM;
  src: local("ASTOM"),
    url(/static/media/ASTOM.82c2a4fc.TTF) format("truetype");
  font-weight: normal;
}
@font-face{
  font-family: ROBOTO-LIGHT;
  src: local("ROBOTO-LIGHT"),
  url(/static/media/ROBOTO-LIGHT.03e629f6.TTF) format("truetype");
}
@font-face{
  font-family: ROBOTO-MEDIUM;
  src: local("ROBOTO-MEDIUM"),
  url(/static/media/ROBOTO-MEDIUM.d2d7f0c0.TTF) format("truetype");
}
@font-face{
  font-family: ROBOTO-REGULAR;
  src: local("ROBOTO-REGULAR"),
  url(/static/media/ROBOTO-REGULAR.f25bd059.TTF) format("truetype");
}

